@import url("https://fonts.googleapis.com/css2?family=Frank+Ruhl+Libre:wght@300..900&family=Heebo:wght@100..900&family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap");
:root {
  --heb-title-font: "Frank Ruhl Libre", serif;
  --heb-text-font: "Heebo", sans-serif;
  --eng-title-font: "Merriweather", serif;
  --eng-text-font: "Open Sans", sans-serif;
  --bg: #f5f5f5;
  --second-bg: #fffff0;
  --header: #f2e9e4;
  --text: #3d405b;
  --text-highlight: #658585;
  --btn: #e07a5f;
  --highlight: #dbdbdb;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: var(--text);
}

body {
  margin: 0;
  width: 100%;
}

#root {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.heb-title {
  font-family: var(--heb-title-font);
}
.heb-text {
  font-family: var(--heb-text-font);
}

.eng-title {
  font-family: var(--eng-title-font);
}
.eng-text {
  font-family: var(--eng-text-font);
}

/* Navbar: */
@media only screen and (max-width: 1200px) {
  main,
  footer {
    display: none;
  }

  .navbar {
    background-color: var(--header);
    padding: 0 16px;
    min-width: 350px;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .navbar-left {
    font-size: 38px;
    min-width: 260px;
  }
}
